@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

$prefix:'cc-';

// 產險商品 3 x 2
.cathay-comp.cathay-card{
  &.#{$prefix}-layout-top3{

    display: flex;
    flex-wrap: wrap;
    .top3-item{
      position: relative;
      flex: 1 1 100%;
      padding:60px 0px;
      margin-left:3%;
      margin-right:3%;
      margin-top:1.5%;
      margin-bottom:1.5%;
      border:1px solid #00a94f;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-left:0.5%;
        margin-right:0.5%;
        flex: 1 1 32%;
        &:first-child{
          margin-left:1%;
        }

        &:last-child{
          margin-right:1%;
        }
      }
    }

  
    .#{$prefix}title{
      font-size:24px;
      font-weight: 400;
      line-height: 1;
      color:#00a94f;
      letter-spacing: 2px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:20px;
        letter-spacing: 3px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }

    .#{$prefix}subtitle{
      font-size:16px;
      font-weight: 400;
      line-height: 1.5;
      color:#333333;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:18px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}content{
      font-size:16px;
      font-weight: 400;
      line-height: 1;
      color:#333333;

      @media (min-width: map-get($grid-breakpoints, 'md')){
      }
    }


    .#{$prefix}ribbon{
      position: absolute;
      top:20px;
      right:-10px;
      padding:5px 10px;
      color:#fff;
      line-height:1;
      font-size:16px;
      font-weight: 400;
      background-color: #f9556f;
      &:before{
        content: "";
        display: block;
        position: absolute;
        top:25px;
        right:0;
        margin:auto;
        width: 10px;
        height: 10px;
        border-bottom: 9px solid transparent;
        border-left: 10px solid;
        color:#c1243d;

      }
    }
    .#{$prefix}more-content{
      display: none;
      margin:35px 15px 35px 25px;

     
      .mCSB_scrollTools{
          visibility: hidden;
            opacity:0;
      }
      &[status="off"]{
        .mCSB_scrollTools{
            visibility: hidden;
            opacity:0;
        }
      }
      &[status="on"]{
        .mCSB_scrollTools{
            visibility: visible;
            opacity:1;
        }
      }
      height: 250px;
      overflow-y:auto;
      font-size:15px;
      font-weight: 400;
      line-height: 1.5;
      color:#333333;
      text-align:left;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin:35px 8px 35px 15px;
        font-size:18px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        // margin:35px 15px;
      }
      ul,li,p{
        font-size:14px;
        @media (min-width: map-get($grid-breakpoints, 'md')){
          font-size:16px;
        }

        @media (min-width: map-get($grid-breakpoints, 'lg')){
          // margin:35px 15px;
        }
      }
      ul{
        padding-left: 15px;
      }
      .cac-row+.cac-row{
        margin-top: 25px;
      }


    }
    .cac-title{
      font-weight: bold;
      padding-left:15px;
      position: relative;
      &:before{
        content:'';
        position: absolute;
        display:block;
        width: 6px;
        height: 6px;
        background-color:#00a94f;
        top:0;
        bottom:0;
        left:0;
        margin:auto;
        border-radius:50%;
      }
    }
    .#{$prefix}price{
      color:#f9556f;
      font-size:30px;
      font-family:Verdana;
      font-weight: 400;
      line-height: 1;
    }

    .#{$prefix}txt-wrap{
      display: block;
      padding:0px;
      position: relative;
      margin-top: 10px;
      text-align:center;
      width: 100%;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding:0px 5px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}title + .#{$prefix}subtitle{
      margin-top: 15px;
    }
    .#{$prefix}subtitle + .#{$prefix}content{
      margin-top: 25px;
    }
    .#{$prefix}more-content + .#{$prefix}content{
      margin-top: 20px;
    }
    .#{$prefix}content + .#{$prefix}link{
      margin-top: 20px;
    }
    .#{$prefix}link + .#{$prefix}more{
      margin-top: 20px;
    }
    .#{$prefix}link{
      display: inline-block;
      font-size:17px;
      color:#fff;
      background-color: #00a94f;
      padding:15px 40px;
      border-top-left-radius: 50px;
      border-top-right-radius: 50px;
      border-bottom-left-radius: 50px;
      border-bottom-right-radius: 50px;

          
      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding-left:30px;
        padding-right:30px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        padding-left:40px;
        padding-right:40px;
      }
    }
    .#{$prefix}more{
      display:block;
      font-size:16px;
      font-weight: 400;
      line-height: 1;
      color:#333333;
      letter-spacing:0px;
      position: relative;
      cursor:pointer;
      &:hover{
        color:#00a94f;
        &:after{
          border-top: 2px solid #00a94f;
          border-right: 2px solid #00a94f;
        }
      }
      &:after{
        content: "";
        width: 10px;
        height: 10px;
        border-top: 2px solid #666;
        border-right: 2px solid #666;
        transform: rotate(135deg);
        margin-left: 10px;
        position: relative;
        top:-3px;
        display: inline-block;
      }

      &[status="on"]{
        &:after{
            transform: rotate(-45deg);
            top:2px;
        }
      }
    }
    .#{$prefix}img-wrap{
      background-size: cover;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center top;
      margin: 0 auto;
      vertical-align: middle;

      img{
        visibility: hidden;
      }

      $ratio:.5;
      width: round($ratio * 113px);
      height: round($ratio * 131px);
      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        $ratio:.5;
        width: round($ratio * 113px);
        height: round($ratio * 131px);
      }
      
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        $ratio:.7;
        width: round($ratio * 113px);
        height: round($ratio * 131px);
      }
    }
  }
}